<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 工艺编码 -->
            <a-form-model-item
              label="工艺编码"
              prop="craftCode"
              ref="craftCode"
            >
              <a-input
                v-model="entity.craftCode"
                placeholder="请输入工艺编码"
                name="craftCode"
                :disabled="!!ndoEditParams.row"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 工艺名称 -->
            <a-form-model-item
              label="工艺名称"
              prop="name"
              ref="name"
            >
              <a-input
                v-model="entity.name"
                placeholder="请输入工艺名称"
                name="name"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 工艺类型 -->
            <a-form-model-item
              label="工艺类型"
              prop="craftType"
              ref="craftType"
            >
              <a-select
                v-model="entity.craftType"
                placeholder="请选择工艺类型"
                name="craftType"
              >
                <a-select-option value="机械加工工艺">机械加工工艺</a-select-option>
                <a-select-option value="装配工艺">装配工艺</a-select-option>
                <a-select-option value="焊接工艺">焊接工艺</a-select-option>
                <a-select-option value="热处理工艺">热处理工艺</a-select-option>
                <a-select-option value="表面处理工艺">表面处理工艺</a-select-option>
                <a-select-option value="检验工艺">检验工艺</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 产品类型 -->
            <a-form-model-item
              label="产品类型"
              prop="productType"
              ref="productType"
            >
              <a-select
                v-model="entity.productType"
                placeholder="请选择产品类型"
                name="productType"
              >
                <a-select-option value="金属结构件">金属结构件</a-select-option>
                <a-select-option value="电子产品">电子产品</a-select-option>
                <a-select-option value="机械设备">机械设备</a-select-option>
                <a-select-option value="汽车零部件">汽车零部件</a-select-option>
                <a-select-option value="航空零件">航空零件</a-select-option>
                <a-select-option value="医疗器械">医疗器械</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 版本 -->
            <a-form-model-item
              label="版本"
              prop="version"
              ref="version"
            >
              <a-input
                v-model="entity.version"
                placeholder="请输入版本号"
                name="version"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-select
                v-model="entity.status"
                placeholder="请选择状态"
                name="status"
              >
                <a-select-option value="已发布">已发布</a-select-option>
                <a-select-option value="草稿">草稿</a-select-option>
                <a-select-option value="审核中">审核中</a-select-option>
                <a-select-option value="已废弃">已废弃</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="24">
            <!-- 工艺描述 -->
            <a-form-model-item 
              label="工艺描述" 
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入工艺描述"
                :rows="3"
                :max-length="500"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <!-- 工序清单 -->
      <g-collapse headerName="工序清单">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button class="add-operation-btn" type="primary" icon="plus" @click="addOperation">添加工序</a-button>
            </a-col>
            <a-col :sm="24">
              <a-table 
                :columns="operationColumns" 
                :dataSource="entity.operations" 
                :pagination="false"
                size="small"
                rowKey="id"
              >
                <template slot="sequence" slot-scope="text, record, index">
                  <a-input-number 
                    v-model="entity.operations[index].sequence" 
                    :min="1"
                    style="width: 100%"
                    placeholder="请输入序号" 
                  />
                </template>
                <template slot="operationCode" slot-scope="text, record, index">
                  <a-select
                    v-model="entity.operations[index].operationCode"
                    placeholder="请选择工序"
                    style="width: 100%"
                    @change="(value) => handleOperationChange(value, index)"
                  >
                    <a-select-option v-for="op in operationOptions" :key="op.code" :value="op.code">
                      {{ op.name }}
                    </a-select-option>
                  </a-select>
                </template>
                <template slot="operationName" slot-scope="text, record, index">
                  <span>{{ entity.operations[index].operationName }}</span>
                </template>
                <template slot="department" slot-scope="text, record, index">
                  <span>{{ entity.operations[index].department }}</span>
                </template>
                <template slot="standardTime" slot-scope="text, record, index">
                  <a-input-number 
                    v-model="entity.operations[index].standardTime" 
                    :min="0"
                    :precision="2"
                    style="width: 100%"
                    placeholder="请输入标准工时" 
                  />
                </template>
                <template slot="standardUnit" slot-scope="text, record, index">
                  <a-select
                    v-model="entity.operations[index].standardUnit"
                    placeholder="请选择单位"
                    style="width: 100%"
                  >
                    <a-select-option value="秒">秒</a-select-option>
                    <a-select-option value="分钟">分钟</a-select-option>
                    <a-select-option value="小时">小时</a-select-option>
                  </a-select>
                </template>
                <template slot="remark" slot-scope="text, record, index">
                  <a-input v-model="entity.operations[index].remark" placeholder="请输入备注" />
                </template>
                <template slot="action" slot-scope="text, record, index">
                  <a-button 
                    type="danger" 
                    icon="delete" 
                    size="small" 
                    @click="() => removeOperation(index)"
                  />
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 工艺要求 -->
      <g-collapse headerName="工艺要求">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button class="add-requirement-btn" type="primary" icon="plus" @click="addRequirement">添加要求</a-button>
            </a-col>
            <a-col :sm="24">
              <a-table 
                :columns="requirementColumns" 
                :dataSource="entity.requirements" 
                :pagination="false"
                size="small"
                rowKey="id"
              >
                <template slot="category" slot-scope="text, record, index">
                  <a-select
                    v-model="entity.requirements[index].category"
                    placeholder="请选择类别"
                    style="width: 100%"
                  >
                    <a-select-option value="质量要求">质量要求</a-select-option>
                    <a-select-option value="安全要求">安全要求</a-select-option>
                    <a-select-option value="工艺要求">工艺要求</a-select-option>
                    <a-select-option value="设备要求">设备要求</a-select-option>
                    <a-select-option value="材料要求">材料要求</a-select-option>
                    <a-select-option value="环境要求">环境要求</a-select-option>
                    <a-select-option value="其他要求">其他要求</a-select-option>
                  </a-select>
                </template>
                <template slot="content" slot-scope="text, record, index">
                  <a-textarea
                    v-model="entity.requirements[index].content"
                    placeholder="请输入要求内容"
                    :rows="2"
                    :auto-size="{ minRows: 2, maxRows: 4 }"
                  />
                </template>
                <template slot="importance" slot-scope="text, record, index">
                  <a-select
                    v-model="entity.requirements[index].importance"
                    placeholder="请选择重要性"
                    style="width: 100%"
                  >
                    <a-select-option value="高">高</a-select-option>
                    <a-select-option value="中">中</a-select-option>
                    <a-select-option value="低">低</a-select-option>
                  </a-select>
                </template>
                <template slot="action" slot-scope="text, record, index">
                  <a-button 
                    type="danger" 
                    icon="delete" 
                    size="small" 
                    @click="() => removeRequirement(index)"
                  />
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 元数据信息 -->
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditCraft",
  mixins: [GNdoPageEditBase],
  
  data() {
    return {
      loading: false,
      entity: {
        id: undefined,
        craftCode: "",
        name: "",
        craftType: "",
        productType: "",
        version: "",
        description: "",
        operations: [],
        requirements: [],
        status: "草稿",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      },
      operationOptions: [
        // 加工工序
        { code: "OP00101", name: "车削", department: "机加部", time: 30, unit: "分钟" },
        { code: "OP00202", name: "铣削", department: "机加部", time: 45, unit: "分钟" },
        { code: "OP00303", name: "钻孔", department: "机加部", time: 25, unit: "分钟" },
        { code: "OP00404", name: "磨削", department: "机加部", time: 40, unit: "分钟" },
        // 装配工序
        { code: "AS00105", name: "组装主体", department: "装配部", time: 60, unit: "分钟" },
        { code: "AS00206", name: "连接零件", department: "装配部", time: 45, unit: "分钟" },
        { code: "AS00307", name: "固定紧固", department: "装配部", time: 30, unit: "分钟" },
        // 检验工序
        { code: "IN00108", name: "尺寸检验", department: "质检部", time: 20, unit: "分钟" },
        { code: "IN00209", name: "外观检验", department: "质检部", time: 15, unit: "分钟" },
        { code: "IN00310", name: "功能测试", department: "质检部", time: 30, unit: "分钟" },
        // 热处理工序
        { code: "HT00111", name: "淬火", department: "热处理部", time: 120, unit: "分钟" },
        { code: "HT00212", name: "回火", department: "热处理部", time: 180, unit: "分钟" },
        { code: "HT00313", name: "退火", department: "热处理部", time: 240, unit: "分钟" },
        // 表面处理工序
        { code: "SF00114", name: "喷漆", department: "表面处理部", time: 50, unit: "分钟" },
        { code: "SF00215", name: "电镀", department: "表面处理部", time: 90, unit: "分钟" },
        { code: "SF00316", name: "抛光", department: "表面处理部", time: 40, unit: "分钟" },
        // 包装工序
        { code: "PK00117", name: "内包装", department: "包装部", time: 15, unit: "分钟" },
        { code: "PK00218", name: "外包装", department: "包装部", time: 20, unit: "分钟" }
      ],
      rules: {
        craftCode: [
          { required: true, message: "请输入工艺编码", trigger: "change" },
          { pattern: /^[A-Z]{2}\d{3}/, message: "工艺编码格式不正确", trigger: "change" }
        ],
        name: [
          { required: true, message: "请输入工艺名称", trigger: "change" },
          { pattern: /^[^\s]+(\s+[^\s]+)*$/, message: "工艺名称不能包含前后空格", trigger: "change" }
        ],
        craftType: [
          { required: true, message: "请选择工艺类型", trigger: "change" }
        ],
        productType: [
          { required: true, message: "请选择产品类型", trigger: "change" }
        ],
        version: [
          { required: true, message: "请输入版本号", trigger: "change" },
          { pattern: /^V\d+\.\d+$/, message: "版本号格式不正确，应为Vx.x格式", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择状态", trigger: "change" }
        ]
      },
      operationColumns: [
        {
          title: "序号",
          dataIndex: "sequence",
          key: "sequence",
          width: "8%",
          scopedSlots: { customRender: "sequence" }
        },
        {
          title: "工序",
          dataIndex: "operationCode",
          key: "operationCode",
          width: "20%",
          scopedSlots: { customRender: "operationCode" }
        },
        {
          title: "工序名称",
          dataIndex: "operationName",
          key: "operationName",
          width: "15%",
          scopedSlots: { customRender: "operationName" }
        },
        {
          title: "部门",
          dataIndex: "department",
          key: "department",
          width: "12%",
          scopedSlots: { customRender: "department" }
        },
        {
          title: "标准工时",
          dataIndex: "standardTime",
          key: "standardTime",
          width: "12%",
          scopedSlots: { customRender: "standardTime" }
        },
        {
          title: "单位",
          dataIndex: "standardUnit",
          key: "standardUnit",
          width: "10%",
          scopedSlots: { customRender: "standardUnit" }
        },
        {
          title: "备注",
          dataIndex: "remark",
          key: "remark",
          width: "15%",
          scopedSlots: { customRender: "remark" }
        },
        {
          title: "操作",
          key: "action",
          width: "8%",
          scopedSlots: { customRender: "action" }
        }
      ],
      requirementColumns: [
        {
          title: "类别",
          dataIndex: "category",
          key: "category",
          width: "15%",
          scopedSlots: { customRender: "category" }
        },
        {
          title: "要求内容",
          dataIndex: "content",
          key: "content",
          width: "65%",
          scopedSlots: { customRender: "content" }
        },
        {
          title: "重要性",
          dataIndex: "importance",
          key: "importance",
          width: "10%",
          scopedSlots: { customRender: "importance" }
        },
        {
          title: "操作",
          key: "action",
          width: "10%",
          scopedSlots: { customRender: "action" }
        }
      ]
    };
  },
  
  methods: {
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取工艺数据
        setTimeout(() => {
          this.initFormData();
          this.loading = false;
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    
    resetFormData() {
      this.entity = {
        id: undefined,
        craftCode: "",
        name: "",
        craftType: "",
        productType: "",
        version: "",
        description: "",
        operations: [],
        requirements: [],
        status: "草稿",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      };
    },
    
    initFormData() {
      if (this.ndoEditParams.row) {
        const row = this.ndoEditParams.row;
        this.entity = {
          ...this.entity,
          ...row,
          // 确保数组被正确初始化
          operations: row.operations || [],
          requirements: row.requirements || []
        };
      }
    },
    
    async save() {
      // 保存校验
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        // 模拟保存操作
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.name);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e) {
      console.log(e.target.value);
    },
    
    // 处理工序选择变更
    handleOperationChange(value, index) {
      const selectedOperation = this.operationOptions.find(op => op.code === value);
      if (selectedOperation) {
        this.entity.operations[index].operationName = selectedOperation.name;
        this.entity.operations[index].department = selectedOperation.department;
        this.entity.operations[index].standardTime = selectedOperation.time;
        this.entity.operations[index].standardUnit = selectedOperation.unit;
      }
    },
    
    // 添加工序
    addOperation() {
      // 计算下一个序号
      const nextSequence = this.entity.operations.length > 0 
        ? Math.max(...this.entity.operations.map(op => op.sequence)) + 10 
        : 10;
      
      this.entity.operations.push({
        id: Date.now(), // 使用时间戳作为临时ID
        sequence: nextSequence,
        operationCode: "",
        operationName: "",
        department: "",
        standardTime: 0,
        standardUnit: "分钟",
        remark: ""
      });
    },
    
    // 删除工序
    removeOperation(index) {
      this.entity.operations.splice(index, 1);
    },
    
    // 添加工艺要求
    addRequirement() {
      this.entity.requirements.push({
        id: Date.now(), // 使用时间戳作为临时ID
        category: "",
        content: "",
        importance: "中"
      });
    },
    
    // 删除工艺要求
    removeRequirement(index) {
      this.entity.requirements.splice(index, 1);
    }
  },
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.add-operation-btn,
.add-requirement-btn {
  margin-bottom: 16px;
}
</style> 